/*homepage*/

.home {
  text-align: center;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

.header {
  margin-top: 1.75rem;
  margin-bottom: 1.75rem;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 45px;
  font-weight: 700;
  text-transform: uppercase;
}

.nav {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1.5rem;
  line-height: 2rem;
}

.content {
  width: 100%;
  padding: 1rem;
}

@media (min-width: 640px) {
  .content {
    display: flex;
    flex-direction: row-reverse;
  }
}

.content .image img {
  margin: auto;
}

.content .image {
  display: flex;
}

@media (min-width: 640px) {
  .content .image {
    width: 50%;
  }
}

.content .text {
  margin-top: 2.5rem !important;
  margin-bottom: 2.5rem !important;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !important;
  font-size: 2.25rem !important;
  line-height: 2.5rem !important;
}

@media (min-width: 640px) {
  .content .text {
    width: 50% !important;
  }
}

/*404*/

.notfound {
  margin-top: 5rem;
  text-align: center;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 4.5rem;
  line-height: 1;
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

/*parts, inventions*/

.partImgDiv,  .invImgDiv {
  display: flex;
}

.partImg,  .invImg {
  margin: auto;
  max-height: 24rem;
  max-width: 100%;
  border-radius: 1rem;
  padding: 0.5rem;
}

.part,  .invention {
  margin-top: 1.25rem;
  margin-right: 1.25rem;
  border-radius: 1rem;
  --tw-bg-opacity: 1;
  background-color: rgb(34 34 34 / var(--tw-bg-opacity));
  padding: 1rem;
  text-align: center;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

.partDesc,  .invDesc {
  margin-top: 1rem;
}

.partDescLine,  .invDescLine {
  margin-top: 0.5rem;
}

.partCost,  .invCost {
  margin-top: 0.75rem;
  font-size: 30px;
}

.partName,  .invName {
  font-size: 40px;
}

.partList,  .invList {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  padding: 0.5rem;
}

@media (min-width: 640px) {
  .partList,  .invList {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (min-width: 1024px) {
  .partList,  .invList {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

.partSearch {
  display: flex;
}

.partSearch input {
  margin: auto;
  margin-top: 2rem;
  height: 3.5rem;
  width: 20rem;
  border-radius: 1rem;
  --tw-bg-opacity: 1;
  background-color: rgb(34 34 34 / var(--tw-bg-opacity));
  padding: 1rem;
  font-size: 20px;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

.partSearch input:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.invCreator {
  margin-top: 1rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.addToCart {
  margin-top: 1rem;
  height: 1.75rem;
  border-radius: 1rem;
  background-color: rgb(30 58 138 / 0.1);
}

.addToCart:hover {
  cursor: pointer;
}

.styledForm {
  margin: auto;
  margin-top: 2.5rem;
  width: 100%;
  border-radius: 1rem;
  background-color: rgb(24 56 89 / 0.2);
  padding: 2.5rem;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

@media (min-width: 450px) {
  .styledForm {
    width: 85%;
  }
}

@media (min-width: 540px) {
  .styledForm {
    width: 75%;
  }
}

@media (min-width: 640px) {
  .styledForm {
    width: 60%;
  }
}

@media (min-width: 768px) {
  .styledForm {
    width: 45%;
  }
}

.styledSubmit button {
  margin: auto;
  margin-top: 50px;
  height: 80px;
  width: 75%;
  border-radius: 20px;
  background-color: rgb(65 93 175 / 0.2);
  font-size: 30px;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.styledSubmit button:hover {
  cursor: pointer;
  background-color: rgb(0 45 175 / 0.2);
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

.styledSubmit button:active {
  background-color: rgb(0 45 175 / 0.1);
}

.styledSubmit,  .styledInput,  .invalidInput {
  display: flex;
}

.styledInput input,  .invalidInput input {
  margin: auto;
  margin-top: 2.5rem;
  height: 65px;
  border-radius: 1rem;
  background-color: rgb(13 37 64 / 0.4);
  padding: 1rem;
  font-size: 25px;
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.invalidInput input {
  --tw-text-opacity: 1 !important;
  color: rgb(239 68 68 / var(--tw-text-opacity)) !important;
  caret-color: #d1d5db !important;
}

.afterVerify {
  margin: auto;
  margin-top: 10%;
  text-align: center;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 50px;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

.verifyWrapper {
  display: flex;
}

.dashboard {
  display: flex;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

.uInfo {
  margin: auto;
  margin-top: 5rem;
  height: 200px;
  width: 100%;
  border-radius: 1rem;
  background-color: rgb(30 58 138 / 0.2);
  text-align: center;
}

@media (min-width: 640px) {
  .uInfo {
    height: 24vw;
    width: 50%;
  }
}

.uName {
  margin-top: 1.25rem;
  font-size: 40px;
  font-weight: 700;
}

@media (min-width: 640px) {
  .uName {
    font-size: 5vw;
  }
}

.uBalance {
  font-size: 25px;
}

@media (min-width: 640px) {
  .uBalance {
    font-size: 3vw;
  }
}

.relative {
  position: relative;
}

.m-auto {
  margin: auto;
}

.my-2 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.my-5 {
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}

